// src/components/ProfileModal.js
import React, { useState } from 'react';
import { useAuth } from '../context/AuthContext';
import './ProfileModal.css';

const ProfileModal = ({ isOpen, onClose }) => {
    const { user, token, changePassword, updateUserProfile } = useAuth(); // ดึง token และ user มาจาก AuthContext
    const [currentPassword, setCurrentPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [selectedFile, setSelectedFile] = useState(null);
    const [preview, setPreview] = useState(null);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setSuccess('');

        if (newPassword !== confirmPassword) {
            setError("รหัสผ่านใหม่ไม่ตรงกัน");
            return;
        }

        // เพิ่มการตรวจสอบความซับซ้อนของรหัสผ่านให้ปลอดภัยยิ่งขึ้น
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d).{8,}$/;
        if (!passwordRegex.test(newPassword)) {
            setError("รหัสผ่านใหม่ต้องมีอย่างน้อย 8 ตัวอักษร, ประกอบด้วยตัวพิมพ์ใหญ่, พิมพ์เล็ก, และตัวเลข");
            return;
        }

        setIsLoading(true);
        const result = await changePassword(currentPassword, newPassword);
        setIsLoading(false);

        if (result.success) {
            setSuccess(result.message || 'เปลี่ยนรหัสผ่านสำเร็จ!');
            setTimeout(() => {
                handleClose();
            }, 2000);
        } else {
            setError(result.message || 'การเปลี่ยนรหัสผ่านล้มเหลว');
        }
    };

    const handleFileChange = (e) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            setSelectedFile(file);
            setPreview(URL.createObjectURL(file));
        }
    };

    const handleImageUpload = async () => {
        if (!selectedFile) {
            setError("กรุณาเลือกไฟล์รูปภาพก่อน");
            return;
        }
        setIsLoading(true);
        setError('');
        setSuccess('');

        const formData = new FormData();
        formData.append('profileImage', selectedFile);

        try {
            const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:3001';
            const response = await fetch(`${apiUrl}/api/auth/upload-profile-image`, {
                method: 'POST',
                headers: {
                    // ส่ง token ไปกับ request เพื่อยืนยันตัวตน (สำคัญมาก)
                    'Authorization': `Bearer ${token}`,
                },
                body: formData,
                // ไม่ต้องตั้งค่า 'Content-Type' header เองเมื่อใช้ FormData
                // browser จะจัดการให้โดยอัตโนมัติ
            });
            const result = await response.json();

            if (!response.ok) {
                throw new Error(result.message || 'การอัปโหลดรูปภาพล้มเหลว');
            }

            setSuccess(result.message);
            // Use the function from AuthContext to update the user state locally
            updateUserProfile({ profileImageUrl: result.imageUrl });

        } catch (err) {
            setError(err.message || 'การอัปโหลดรูปภาพล้มเหลว');
        } finally {
            setIsLoading(false);
        }
    };

    const handleClose = () => {
        // รีเซ็ตค่าทั้งหมดเมื่อปิดหน้าต่าง
        setCurrentPassword('');
        setNewPassword('');
        setConfirmPassword('');
        setError('');
        setSuccess('');
        setSelectedFile(null);
        setPreview(null);
        onClose();
    };

    if (!isOpen) {
        return null;
    }

    // --- Logic to safely create the profile image URL ---
    const defaultAvatar = 'https://via.placeholder.com/150';
    let profileImageSrc = defaultAvatar;

    if (preview) {
        // 1. ถ้ามีการเลือกรูปใหม่ ให้ใช้รูปที่ preview อยู่
        profileImageSrc = preview;
    } else if (user && user.profileImageUrl) {
        // 2. ถ้ามี URL รูปโปรไฟล์จาก user object
        const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:3001';
        // สร้าง URL ที่สมบูรณ์โดยการรวม API URL กับ path ของรูปภาพ
        profileImageSrc = `${apiUrl}${user.profileImageUrl}`;
    }
    // 3. ถ้าไม่มีทั้งสองอย่าง จะใช้ defaultAvatar ที่กำหนดไว้ข้างต้น

    return (
        <div className="modal-overlay" onClick={handleClose}>
            <div className="modal-content profile-modal" onClick={e => e.stopPropagation()}>
                <button className="close-button" onClick={handleClose}>&times;</button>
                <h2>โปรไฟล์ของฉัน</h2>
                
                <div className="profile-section profile-picture-section">
                    <img 
                        src={profileImageSrc}
                        alt="Profile" 
                        className="profile-picture"
                    />
                    <input 
                        type="file" 
                        id="profile-upload" 
                        style={{ display: 'none' }} 
                        onChange={handleFileChange}
                        accept="image/png, image/jpeg"
                    />
                    <label htmlFor="profile-upload" className="upload-button">เลือกรูปภาพ</label>
                    {selectedFile && (
                        <button onClick={handleImageUpload} className="submit-button" disabled={isLoading}>
                            {isLoading ? 'กำลังอัปโหลด...' : 'บันทึกรูปภาพ'}
                        </button>
                    )}
                </div>

                <h3 className="section-title">เปลี่ยนรหัสผ่าน</h3>
                <form onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label htmlFor="current-password">รหัสผ่านปัจจุบัน</label>
                        <input
                            type="password"
                            id="current-password"
                            value={currentPassword}
                            onChange={(e) => setCurrentPassword(e.target.value)}
                            required
                            disabled={isLoading}
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="new-password">รหัสผ่านใหม่</label>
                        <input
                            type="password"
                            id="new-password"
                            value={newPassword}
                            onChange={(e) => setNewPassword(e.target.value)}
                            required
                            disabled={isLoading}
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="confirm-password">ยืนยันรหัสผ่านใหม่</label>
                        <input
                            type="password"
                            id="confirm-password"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            required
                            disabled={isLoading}
                        />
                    </div>
                    {error && <p className="error-message">{error}</p>}
                    {success && <p className="success-message">{success}</p>}
                    <button type="submit" className="submit-button" disabled={isLoading || !currentPassword || !newPassword}>
                        {isLoading ? 'กำลังบันทึก...' : 'เปลี่ยนรหัสผ่าน'}
                    </button>
                </form>
            </div>
        </div>
    );
};

export default ProfileModal;